import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItemText from '@material-ui/core/ListItemText';
import axios from 'axios';
import { connect } from 'react-redux';
import { UPDATE_BUNDLE } from '../actions/types';
import ReactQuill from 'react-quill';
import socketIOClient from 'socket.io-client';
import { ListItemSecondaryAction, IconButton, TextField, InputAdornment, LinearProgress } from '@material-ui/core';
import FiberNewIcon from '@material-ui/icons/FiberNew';
import SendIcon from '@material-ui/icons/Send';
import { ChatFeed, Message } from 'react-chat-ui';

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  chat_box: {
    paddingTop: 10,
    paddingLeft: 5, 
    paddingRight: 5
  },
  paper_chat: {
    height: 320,
    paddingTop: 10,
    paddingLeft: 5, 
    paddingRight: 5,
    overflow: 'scroll'
  },
  paper_send: {
    padding: 10
  },
  root_list: {
    flexGrow: 1,
    overflow: 'auto',
    height: 350
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: 'center',
    color: theme.palette.text.secondary,
    overflow: 'hidden'
  },
  paper_list: {
    textAlign: 'center',
    paddingTop: theme.spacing.unit,
    color: theme.palette.text.secondary,
  },
  listSection: {
    backgroundColor: 'inherit',
  },
  ul: { 
    backgroundColor: 'inherit',
    padding: 0,
  },
  date: {
    backgroundColor: '#fff'
  }
});
class GlanceBulletin extends React.Component {
  state ={
    currentBulletin: { parent: '', currentElement: { reach: [], name: 'Please select an event', audience: [], media: [], id: '', description: '' } },
    description: '',
    socketAuth: false,
    bulletin_list: '',
    draftChat: {},
    textMessage: '',
    loading: false,
    connection: false,
    messages: [],
    draft: JSON.parse(sessionStorage.getItem('glance_bulletin_data')) ? JSON.parse(sessionStorage.getItem('glance_bulletin_data')) : [],
  }
  socket = null;
  loadEvent = (number1, number2) => {
    if(this.state.loading) return;
    if(this.state.connection === false) return;
    let toLoad = this.state.draft[number1].data[number2];
    if(this.state.draftChat && this.state.draftChat[toLoad.id] === undefined)
      this.socket.emit('synchronize', JSON.stringify({ bulletin: toLoad.id }));
    document.getElementById('new-'+toLoad.id).style.width = 0;
    this.setMessagesFor(toLoad.id);
    this.setState({ currentBulletin: { parent: number1, currentElement: {...toLoad, audience: toLoad.audience.split(',') } }, description: toLoad.description });
  }
  componentDidMount() {
    this.setState({loading: true});
    axios.post('/web/bundle-check', { hashsum: this.props.auth.bundle.hashsum ,email: this.props.auth.user_email }, { headers: {'x-access-token': this.props.auth.user_token } })
      .then(response => {
        if(!response.data.error){
          if(response.data.bundle) {
            this.props.updateBundle({
              bundle: response.data.bundle
            });
          }
        }
      })
      .then( () => {
        axios.post('/web/bulletin-data-from-list', { bulletin_list: this.props.auth.bundle.bundle.bulletins.slice((this.props.auth.bundle.bundle.bulletins.length - 100), this.props.auth.bundle.bundle.bulletins.length) ,email: this.props.auth.user_email }, { headers: {'x-access-token': this.props.auth.user_token } })
          .then(response => {
            if(!response.data.error){
              if(response.data.data) {
                if (this.unmounted) return;
                let i,j;
                let bulletin_list = '';
                for(i=0;i<response.data.data.length;i++) {
                  for(j=0;j<response.data.data[i].data.length;j++) {
                    bulletin_list += response.data.data[i].data[j].id + ',';
                  }
                }
                bulletin_list = bulletin_list.slice(0, -1);
                this.setState({ draft: response.data.data.reverse(), bulletin_list: bulletin_list });
                this.configureSocket(bulletin_list);
              }
            }
          })
          .then(() => {
            if (this.unmounted) return;
            this.setState({loading: false});
          });
      });

  }

  setMessagesFor = (id) => {
    let currentMessages = this.state.draftChat[id] !== undefined ? this.state.draftChat[id] : [];
    currentMessages.sort(
      function(a, b) {
        var dateA = new Date(a.timestamp), dateB = new Date(b.timestamp);
        return dateA - dateB;
      }
    );
    let toSet = [];
    let i;
    for(i=0;i<currentMessages.length;i++) {
      toSet.push(new Message({
        id: currentMessages[i].email === this.props.auth.user_email ? 0 : currentMessages[i].email,
        message: currentMessages[i].message,
        senderName: currentMessages[i].email
      }));
    } 
    this.setState({messages: toSet});
  }

  configureSocket = (bulletin_list) => {
    this.socket = socketIOClient('https://dock-chat.herokuapp.com/');
    this.socket.on('feedback', (data) => {
      if(this.unmounted) return;
      try {
        data = JSON.parse(data);
        if(data && !data.error) {
          if(data.data) {
            switch(data.data) {
            case 500:
              
              this.authSocket(bulletin_list);
              this.setState({connection: false});
              break;
            case 101:
              
              this.setState({connection: true});
              break;
            case 105:
              
              this.setCurrentMessages(data.payload);
              break;
            default:
              break;
            }
          }
        }
      } catch (err) { this.setState(); }
    });
    this.socket.on('new_message_in_bulletin', (data) => {
      try{
        data = JSON.parse(data);
        if(!data.error) {
          let toSet = this.state.draftChat;
          if (!toSet[data.bulletin]){
            toSet[data.bulletin] = [];
            toSet[data.bulletin].push({ email: data.email, message: data.data, timestamp: data.timestamp });
          } else {
            toSet[data.bulletin].push({ email: data.email, message: data.data, timestamp: data.timestamp });
          }
          if(data.email !== this.props.auth.user_email)
            document.getElementById('new-'+data.bulletin).style.width = null;
          
          this.setState({ draftChat: toSet });
          if ( data.bulletin === this.state.currentBulletin.currentElement.id ){
            this.setMessagesFor(data.bulletin);
          }
        }
      } catch (e) {
        this.setState();
      }
    });
  }

  setCurrentMessages = (messages) => {
    let toSet = this.state.draftChat;
    toSet[messages.bulletin_id] = messages.messages ? messages.messages : [];
    this.setState({ draftChat: toSet});
    if ( messages.bulletin_id === this.state.currentBulletin.currentElement.id ){
      this.setMessagesFor(messages.bulletin_id);
    }
  }

  componentDidUpdate() {
    if (document.getElementById('dope'))
      document.getElementById('dope').scrollTop = document.getElementById('dope').scrollHeight;
  }

  authSocket = (bulletin_list) => {
    if(this.socket) {
      let toSend = { token: this.props.auth.user_token, bulletins: bulletin_list };
      this.socket.emit('auth request', JSON.stringify(toSend));
    } else {
      return;
    }
  }

  sendMessage = () => {
    if(this.state.textMessage !== '' || this.state.textMessage.length > 900){
      this.socket.emit('send to bulletin', JSON.stringify({ bulletin: this.state.currentBulletin.currentElement.id, message: this.state.textMessage }));
      this.setState({ textMessage: '' });
    }
  }

  componentWillUnmount() {
    sessionStorage.setItem('glance_bulletin_data',JSON.stringify(this.state.draft));
    this.unmounted = true;
  }

  handleChangeDescription() {    
    this.setState({
      description: this.state.currentBulletin.currentElement.description
    });
  }

  handleChangePass = prop => event => {
    this.setState({ [prop]: event.target.value });
  };

  render() {
    const { classes } = this.props;
    const { draft } = this.state;
    return(
      <div className={classes.root}>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={4} >
          
            <Paper className={classes.paper_list}>
              <Typography variant="title" align="center">
                Bulletin List
              </Typography>
              <div className={classes.root_list} >
                <List className={classes.root} subheader={<li />}>
                  {draft && draft.map((currentRange, number1) => (
                    <li key={`section-${currentRange.date}-${number1}`} className={classes.listSection}>
                      <ul className={classes.ul}>
                        <ListSubheader className={classes.date}>{`Created on ${new Date(currentRange.date).getDate()}-${new Date(currentRange.date).getMonth()}-${new Date(currentRange.date).getFullYear()}`}</ListSubheader>
                        {currentRange.data.map((item, number2) => (
                          
                          <ListItem disabled={!this.state.connection} button onClick={() => this.loadEvent(number1, number2)} key={`item-${item.name}-${item}-${number2}`}>
                            <ListItemText primary={`${item.name}`} />
                            <ListItemSecondaryAction>
                              <FiberNewIcon id={'new-'+item.id} style={{width: 0}}/>
                            </ListItemSecondaryAction>
                          </ListItem>
                        ))}
                      </ul>
                    </li>
                  ))}
                  {
                    !this.state.loading &&  draft && draft.length === 0 && 
                    <ListItem divider={true} button>
                      <ListItemText primary={'No Bulletins Created Yet'} secondary={'You can live chat with bulletin audience here'} />
                    </ListItem>
                  }
                </List>
              </div>
              {
                !this.state.loadingData &&  draft && draft.length === 0 && <Typography style={{margin: 10}} variant='caption'> You have not created any bulletins yet, to get started check out the create tab </Typography>
              }
              {this.state.loading && <LinearProgress/>}
            </Paper>
          </Grid>

          { this.state.currentBulletin.parent !== '' && 
          <Grid container item xs={12} sm={8} spacing={16} >
            <Grid item xs={12} sm={4} >
              <Paper style={{height:100, marginBottom: 15}} className={classes.paper}>
                <Typography variant="title">Reach</Typography>
                <Typography variant="display3">
                  {this.state.currentBulletin.currentElement.reach.length}
                </Typography>
              </Paper>
              <ReactQuill onChange={this.handleChangeDescription.bind(this)} style={{ marginBottom: 5}} value={this.state.currentBulletin.currentElement.description} placeholder="Event description here" />
              
            </Grid>
            <Grid item xs={12} sm={8} >
              {/* <Paper className={classes.paper}>
                
                {JSON.stringify(this.state.draftChat[this.state.currentBulletin.currentElement.id])}
              </Paper> */}
              <Grid item xs={12}>
                <Paper id='dope' className={classes.paper_chat}>
                  <ChatFeed
                    messages={this.state.messages} // Boolean: list of message objects
                    isTyping={false} // Boolean: is the recipient typing
                    hasInputField={false} // Boolean: use our input, or use your own
                    showSenderName // show the name of the user who sent the message
                    bubblesCentered={false} //Boolean should the bubbles be centered in the feed?
                    // JSON: Custom bubble styles
                    bubbleStyles={
                      {
                        text: {
                          fontSize: 20
                        },
                        chatbubble: {
                          borderRadius: 70,
                          padding: 15
                        }
                      }
                    }
                  />
                </Paper>
              </Grid>
              <Grid item xs={12}>
                <Paper className={classes.paper_send}>
                  <TextField
                    id="text-message"
                    placeholder="Send a message"
                    fullWidth
                    value={this.state.textMessage}
                    onChange={this.handleChangePass('textMessage')}
                    onKeyPress={(ev) => {
                      if (ev.key === 'Enter') {
                        this.sendMessage();
                      }
                    }}
                    InputProps={{
                      endAdornment: <InputAdornment position="end">
                        <IconButton onClick={this.sendMessage}>
                          <SendIcon/>
                        </IconButton>
                      </InputAdornment>
                    }}
                    margin="normal"
                  />
                </Paper>
              </Grid>
            </Grid>
            
          </Grid>
          }
          
        </Grid>
      </div>
    );
  }
}
GlanceBulletin.propTypes = {
  classes: PropTypes.object.isRequired,
  updateBundle: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired
};
const mapStateToProps = (state) => {
  return { 
    auth: state.auth
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    updateBundle: (bundle) => {
      dispatch({
        type: UPDATE_BUNDLE,
        payload: bundle
      });
    }
  };
};
// function getRandomColor() {
//   var letters = 'BCDEF'.split('');
//   var color = '#';
//   for (var i = 0; i < 6; i++ ) {
//     color += letters[Math.floor(Math.random() * letters.length)];
//   }
//   return color;
// }
export default connect(mapStateToProps, mapDispatchToProps) (withStyles(styles) (GlanceBulletin));


