import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import PropTypes from 'prop-types';
import { Route, Switch } from 'react-router-dom';
import RequireAuth from '../components/auth/require_auth';
import { connect } from 'react-redux';


import GlanceBulletin from '../components/GlanceBulletin';
import CreateBulletin from '../components/CreateBulletin';

const styles = theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    position: 'relative',
    overflow: 'auto',
    maxHeight: 300,
  },
  listSection: {
    backgroundColor: 'inherit',
  },
  ul: {
    backgroundColor: 'inherit',
    padding: 0,
  }
});

class Bulletins extends React.Component {
  componentDidMount() {
    if (this.props.selected !== 2) this.props.update();
  }
  render() {
    const { history } = this.props;
    return(
      <div>
        <Tabs style={{marginBottom: 20}} value={history.location.pathname}>
          <Tab
            value='/bulletins'
            label='Glance'
            onClick={() => history.push('/bulletins')}/>
          <Tab
            value='/bulletins/create'
            label='Create'
            onClick={() => history.push('/bulletins/create')}
          />
        </Tabs>
        <Switch>
          <Route exact path="/bulletins" component={RequireAuth(GlanceBulletin)} />
          <Route exact path="/bulletins/create" component={RequireAuth(CreateBulletin)} />
        </Switch>
      </div>
    );
  }
}
Bulletins.propTypes = {
  classes: PropTypes.object.isRequired,
  selected: PropTypes.number.isRequired,
  update: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired
};

const mapStateToProps = (state) => {
  return { selected: state.status.selected };
};
const mapDispatchToProps = (dispatch) => {
  return {
    update: () => {
      dispatch({
        type: 'UPDATE_ROUTE',
        payload: 2
      });
    }
  };
};
export default connect(mapStateToProps, mapDispatchToProps) (withStyles(styles) (Bulletins));


