import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import ReactQuill from 'react-quill';
import Dropzone from 'react-dropzone';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import Snackbar from '@material-ui/core/Snackbar';
import axios from 'axios';
import green from '@material-ui/core/colors/green';
import classNames from 'classnames';
import CircularProgress from '@material-ui/core/CircularProgress';
import { connect } from 'react-redux';
import Tree from './Tree';

const styles = (theme) => ({
  root: {
    padding: 20
  },
  myHolder: {
    padding: 5,
    margin: 5
  },
  customHelper: {
    color: 'rgba(0, 0, 0, 0.54)',
    margin: '0',
    fontSize: '0.75rem',
    textAlign: 'left',
    marginTop: '8px',
    minHeight: '1em',
    fontFamily: '"Roboto", "Helvetica", "Arial","sans-serif" ',
    lineHeight: '1em',
    display: 'block',
    marginBefore: '1em',
    marginAfter: '1em',
    marginStart: '0px',
    marginEnd: '0px',
  },
  wrapper: {
    margin: theme.spacing.unit,
    position: 'relative',
  },
  buttonSuccess: {
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[700],
    },
  },
  buttonProgress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  },
});

class CreateBulletin extends React.Component {
  constructor(props) {
    super(props);
    this.deleteFile = this.deleteFile.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  timer = undefined;
  state = {
    files: [],
    draft: { name: '', description: '' },
    submitting: false,
    open: false,
    mssg: '',
    success: false
  };
  
  handleSubmit = () => {
    const nameErr = this.state.draft.name.length !== 0 && (this.state.draft.name.length <= 3 || this.state.draft.name.length > 60) ? true : false;
    if(nameErr || this.state.draft.name.length === 0) 
      return this.setState({open: true, mssg: 'please provide a valid name'});

    const regex = this.state.draft.description.match(/(<.{1,3}>)(.{0,7}|<br>)(<\/.{1,3}>)/);
    var regexCheck = (regex && regex.index === 0);
    if(regexCheck) {
      regexCheck = (this.state.draft.description.length >= 15)? false : true;
    }
    const descriptionErr = regexCheck || this.state.draft.description.length >= 800 ? true : false;

    if(descriptionErr || this.state.draft.description.length === 0) 
      return this.setState({open: true, mssg: 'please provide a valid description'});

    let files = this.state.files;
    let audienceRaw = sessionStorage.getItem('draft_audience');
    if(!audienceRaw) 
      return this.setState({open: true, mssg: 'please select an audience'});    
    let audience = JSON.parse(audienceRaw).audience;
    let audienceData = [];
    for (const key of Object.keys(audience)) {
      audienceData.push(audience[key].label);
    }
    if( audienceData.length === 0 ) 
      return this.setState({open: true, mssg: 'please select an audience'});
    var formData = new FormData();
    formData.append('name', this.state.draft.name);
    formData.append('description', this.state.draft.description);
    formData.append('email', this.props.auth.user_email);
    formData.append('audience', audienceData);
    let i;
    for(i=0;i<files.length;i++) {
      formData.append('file'+i, files[i]);
    }
    this.setState( { submitting: true, success: false } );
    axios.post('/bulletins/create-bulletin', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        'x-access-token': this.props.auth.user_token
      }
    }).then( response => {
      
      if(!response.data.error) {
        sessionStorage.setItem('draft_audience', JSON.stringify( { audience: [] }) );
        sessionStorage.removeItem('draft_bulletin');
        this.setState({ files: [], draft: { name: '', description: '' }, success: true, submitting: false, open: true, mssg: 'Bulletin was successfully sent'}
          ,() => {
            this.timer = setTimeout(() => {
              this.setState({
                loading: false,
                success: false,
              });
            }, 2000);
          },
        );
      } else {
        this.setState({submitting: false, success: false, open: true, mssg: 'An error has occured, please try again later'});
      }
    })
      .catch( err => {
        if( err.response && err.response.status === 500) {
          window.location.reload();
        }
        this.setState({submitting: false, success: false, open: true, mssg: 'An error has occured, please try again later'});
      });
  
  }
  componentWillMount() {
    const local_draft = sessionStorage.getItem('draft_bulletin');
    if(local_draft) {
      this.setState({draft: JSON.parse(local_draft)});
    }
  }
  componentWillUnmount() {
    sessionStorage.setItem('draft_bulletin',JSON.stringify(this.state.draft));
    clearTimeout(this.timer);
  }
  handleChange = name => event => {
    this.setState({
      draft: { ...this.state.draft, [name]: event.target.value }
    });
  }
  handleChangeDescription(value) {
    if(value.length <= 800 ) {
      this.setState({
        draft: { ...this.state.draft, description: value}
      });
    } else {
      this.setState({
        draft: { ...this.state.draft, description: this.state.draft.description}
      });
    }
  }
  onDrop(acceptedFiles) {
    var toAdd = [];
    var i;
    for(i=1;i<=acceptedFiles.length;i++) {
      toAdd.push(acceptedFiles[i-1]);
      if(i===5) break;
    }
    this.setState({
      files: toAdd
    });
  }
  deleteFile = (current) => {
    if(this.state.submitting) return;
    var toAdd = [...this.state.files];
    toAdd.splice(current, 1);
    this.setState({
      files: toAdd
    });
  }
  handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    this.setState({ open: false });
  };
  render() {
    const { classes } = this.props;
    const nameErr = this.state.draft.name.length !== 0 && (this.state.draft.name.length <= 3 || this.state.draft.name.length > 60) ? true : false;
    const regex = this.state.draft.description.match(/(<.{1,3}>)(.{0,7}|<br>)(<\/.{1,3}>)/);
    var regexCheck = (regex && regex.index === 0);
    if(regexCheck) {
      regexCheck = (this.state.draft.description.length >= 15)? false : true;
    }
    const descriptionErr = regexCheck || this.state.draft.description.length >= 800 ? true : false;
    const submitting = this.state.submitting;
    const success = this.state.success;
    const buttonClassname = classNames({
      [classes.buttonSuccess]: success,
    });
    const loading = submitting;
    return (
      <Paper>
        <div className={classes.root}> 
          <Grid container> 
            <Grid item xs={12} sm={8} className={classes.myHolder} >
              <Typography variant="headline">Bulletin Name</Typography>
              <TextField
                label="Title"
                helperText="You want this to be short and sweet"
                onChange={this.handleChange('name')}
                inputProps={{maxLength: 60, minLength: 4}}
                error={ nameErr }
                fullWidth
                autoFocus
                value={this.state.draft.name}
              />
            </Grid>
            <Grid item xs={12} sm={4}></Grid>
            <Grid item xs={12} sm={8} className={classes.myHolder}>
              <Typography variant="headline" style={{marginBottom:5 }}>Description</Typography>
              <ReactQuill style={{width: 100+'%'}} value={this.state.draft.description} onChange={this.handleChangeDescription.bind(this)} placeholder="Event description here" />  
              <Typography className={classes.customHelper} style={{color: descriptionErr ? '#FF5252':'', fontWeight: descriptionErr ? 'bold':''}}>Description must be between 8 to 800 characters </Typography>
            </Grid>
            <Grid item xs={12}>
              <Dropzone
                onDrop={this.onDrop.bind(this)}
                maxSize={5000000}
                style={{width: 100+'%'}}
                multiple={true}>
                <div>
                  <Button size="small" color="secondary">
                    Click here to upload image or drag them here (max 5 files (5 MB per file))
                  </Button>
                </div>
              </Dropzone>
            </Grid>
            <Grid item xs={12}>
              <List>
                { this.state.files.map((current,number) => {
                  return (
                    <ListItem key={number}>
                      <ListItemText
                        primary={current.name}
                      />
                      <ListItemSecondaryAction>
                        <IconButton onClick={()=>this.deleteFile(number)} aria-label="Delete">
                          <DeleteIcon />
                        </IconButton>
                      </ListItemSecondaryAction>
                    </ListItem>
                  );
                } ) }
              </List>
            </Grid>
            
            <Grid item xs={12} sm={8} className={classes.myHolder} >
              <Typography variant="headline">Select Audience</Typography>
              <Tree hierarchy={this.props.auth.bundle.bundle.hierarchy ? this.props.auth.bundle.bundle.hierarchy : []} />
            </Grid> 
            <Grid item xs={12} sm={4}></Grid>
            
            <Grid item xs={12} className={classes.myHolder} >
              <Snackbar
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'left',
                }}
                open={this.state.open}
                autoHideDuration={2000}
                onClose={this.handleClose}
                message={<span id="message-id">{this.state.mssg}</span>}                
              />
            </Grid> 
            <div className={classes.wrapper}>
              <Button
                variant="raised"
                color="primary"
                className={buttonClassname}
                disabled={loading}
                onClick={this.handleSubmit}
              >
                    Send Bulletin
              </Button>
              {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
            </div>
          </Grid>
        </div>
      </Paper>
    );
  }
}

CreateBulletin.propTypes = {
  classes: PropTypes.object,
  auth: PropTypes.object.isRequired
};
const mapStateToProps = (state) => {
  return { 
    auth: state.auth
  };
};

export default connect(mapStateToProps)(withStyles(styles)(CreateBulletin));



